

.image {
  padding: 12px;
}

.song-container {
  padding: 12px;
}

.song-card {
  color: whitesmoke;
  padding: 12px;
  margin-top: 10px;
}

.song-card:hover {
  background-color: #0b1e2a;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.24), 0 10px 10px rgba(0, 0, 0, 0.20);
  text-decoration: none;
}

body {
  background:linear-gradient(to right bottom, rgb(28, 77, 106), rgb(0, 0, 0)), linear-gradient(transparent, rgb(0, 0, 0) 100%) 100%;
}

.app-header {
  background-color: #282c34;
  min-height: 6vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.dropdown-item {
  cursor: pointer;
}
