body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.image {
  padding: 12px;
}

.song-container {
  padding: 12px;
}

.song-card {
  color: whitesmoke;
  padding: 12px;
  margin-top: 10px;
}

.song-card:hover {
  background-color: #0b1e2a;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.24), 0 10px 10px rgba(0, 0, 0, 0.20);
  text-decoration: none;
}

body {
  background:-webkit-linear-gradient(left top, rgb(28, 77, 106), rgb(0, 0, 0)), -webkit-linear-gradient(transparent, rgb(0, 0, 0) 100%) 100%;
  background:linear-gradient(to right bottom, rgb(28, 77, 106), rgb(0, 0, 0)), linear-gradient(transparent, rgb(0, 0, 0) 100%) 100%;
}

.app-header {
  background-color: #282c34;
  min-height: 6vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.dropdown-item {
  cursor: pointer;
}

